.alertify-dismissible {
    position: relative;
}

.alertify-dismissible .alertify-close {
    position: absolute;
    top: 10px;
    right: 12px;
    background: none;
    border: none;
    font-size: 20px;
    color: inherit;
    cursor: pointer;
}
.wp-block-alertify-dismissible-notice {
    position: relative;
    transition: opacity 0.3s ease-in-out;
}

.alertify-notice-close {
    position: absolute;
    top: 10px;
    right: 10px;
    background: transparent;
    border: none;
    color: inherit;
    font-size: 20px;
    cursor: pointer;
    padding: 5px 10px;
    opacity: 0.7;
    transition: opacity 0.2s ease;
}

.alertify-notice-close:hover {
    opacity: 1;
}